.Nav{position:fixed;top:0;left:0;width:100%;padding:20px 20px;z-index:999;opacity:100%}.Nav #Nav__menu{background-color:rgba(255,255,255,.95);position:fixed;left:0;right:0;top:0;display:flex;justify-content:center;align-items:center;text-align:center;transition:all .5s}.Nav #Nav__menu .Nav__menu__main h1{font-family:"Montserrat Subrayada",sans-serif;margin:25px}.Nav #Nav__menu .Nav__menu__footer{position:fixed;bottom:0;left:0;right:0;justify-content:center;display:flex}.Nav #Nav__menu .Nav__menu__footer h1{font-family:"Montserrat Subrayada",sans-serif;font-size:2em;margin:25px}.Nav .Nav__menu__open{height:100%;opacity:100%}.Nav .Nav__menu__close{overflow:hidden;height:0;opacity:0%}@media only screen and (min-width: 1000px){.Nav{padding:20px 150px}}#nav-icon4{width:calc(1.414213 * calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))));height:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));justify-self:right;transform:rotate(0deg);transition:.5s ease-in-out}#nav-icon4 span{display:block;position:absolute;height:2px;width:100%;background:#2a2a2a;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav-icon4 span:nth-child(1){top:0px;transform-origin:left center}#nav-icon4 span:nth-child(2){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav-icon4 span:nth-child(3){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav-icon4.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav-icon4.open span:nth-child(2){width:0%;opacity:0}#nav-icon4.open span:nth-child(3){transform:rotate(-45deg);top:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));right:0px}
.cursor{width:40px;height:40px;border:2px solid #e08c65;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#e08c65}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#e08c65}html *,body *{cursor:none}
.About__title{display:flex;flex-direction:column;align-items:center;justify-content:center}.About__subtitle{color:rgba(0,0,0,.055);text-align:center;font-size:2.5em}.About__glasses{background-color:wheat}
.Footer{min-height:100vh}.Footer .contact__description{padding:150px 20px;display:flex;flex-direction:column;align-items:flex-end}.Footer .contact__description p{text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(200px + (500 - 200) * ((100vw - 300px) / (1600 - 300)))}.Footer .contact__description p:last-child{margin-top:50px}.Footer .contact__description a{color:#fff}.Footer .contact__description a:hover{color:#2a2a2a}.Footer .copyright{display:flex;position:absolute;bottom:0px;left:0;right:0;justify-content:center}@media only screen and (min-width: 1000px){.Footer .contact__description{padding:50px 150px}}
.Work{padding:0 20px;margin:0 0 100px}.Work .work__category{margin:50px 0;display:flex;justify-content:center;grid-gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.Work .work__category p{padding:15px}.Work .work__category .activeOption{color:#000 !important;background-color:#e08c65}.Work .work__grid{-webkit-column-count:2;column-count:2;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.Work .work__grid .work__item{box-shadow:0 1px 1px 0 rgba(0,0,0,.05),0 1px 10px 0 rgba(0,0,0,.5);display:grid;grid-template-rows:1fr auto;margin-bottom:10px;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.Work .work__grid .work__item img{width:100%;display:block;grid-row:1/-1;grid-column:1}.Work .work__grid .work__item p{background-color:#fff;padding:.2em .5em}.Work .work__grid .work__item:hover p{background-color:#e08c65;color:#fff}@media only screen and (min-width: 700px){.work .work__grid{-webkit-column-count:3;column-count:3}}@media only screen and (min-width: 1000px){.work{padding:0 150px;margin-top:100px}}

*{padding:0;margin:0;box-sizing:border-box}body{scroll-behavior:smooth;overflow-x:hidden}body{font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (20 - 12) * ((100vw - 300px) / (1600 - 300)))}body a{color:#2a2a2a;transition:color .2s;text-decoration:none}body a:hover{color:#e08c65}body h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(5em + (10 - 8) * ((100vw - 300px) / (1600 - 300)))}::selection{background:#e08c65}::-moz-selection{background:#e08c65}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#2a2a2a}
